@import "../../../../assets/styles/app";

.holdersDesktop {
  @include onlyDesktop();

  .header {
    height: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 3fr;
    border-bottom: 1px solid $color_lines;

    p {
      @include centerRow;
      font-weight: 400;
      font-size: 12px;
      line-height: 140%;
      color: $color_body_50;

      &:first-child {
        padding-left: 12px;
        justify-content: flex-start;
      }
    }

  }

  .body {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    .table {

      .row {
        border-bottom: 1px solid $color_lines;
        height: 56px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);

        & > div {
          @include centerRow;

          &:first-child {
            justify-content: flex-start;
            padding-left: 12px;
          }

          &:not(:last-child) {
            position: relative;

            &::after {
              content: "";
              display: block;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              right: 0;
              width: 1px;
              height: 20px;
              background-color: $color_lines;
            }
          }
        }

        .address {

          .dot {
            position: relative;
            width: 16px;
            height: 16px;
            border: 1px solid $color_lines;

            &::after {
              content: "";
              display: block;
              position: absolute;
              width: 6px;
              height: 6px;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              background-color: $color_main;
            }
          }

          p {
            margin-left: 4px;
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            color: $color_body_50;
          }

          button {
            @include button;
            @include centerRow;
            margin-left: 4px;
            width: 16px;
            height: 16px;
          }
        }



      }

    }

    .accountDistribution {

    }
  }
}